import React, { useState } from 'react';
import Hr from '../layout/hr/hr';
import './contact.css';
import emailjs, { init } from 'emailjs-com';
init("user_aHOXGxYRrloM3NiaPcdBs");


function Contact() {

    const [isSubmitted, setIsSubmitted] = useState(false);
    function sendEmail(e: any) {

        e.preventDefault();

        emailjs.sendForm('service_sintal', 'template_aaoaluy', e.target, 'user_aHOXGxYRrloM3NiaPcdBs')
            .then((result: any) => {
                setIsSubmitted(true);
            }, (error: any) => {
                console.log(error.text);
            });
    }

    return (
        <div className="contact">
            <form onSubmit={sendEmail}>
                <div className="form">
                    <h1>לשירותך</h1>
                    <p className='contant-info'>
                        נייד: 050-771-5657 | אימייל: sassoontal@gmail.com
                    </p>
                    <Hr />
                    {!isSubmitted &&
                        <div className="form-inputs">
                            <p className='form-info'>ספרו לי על האירוע שלכם כדי שנוכל להתחיל את התהליך</p>
                            <label htmlFor="name">שם</label>
                            <input type="text" name="customer_name" />
                            <label htmlFor="email">אימייל</label>
                            <input type="email" name="customer_email" />
                            <label htmlFor="number">מספר טלפון</label>
                            <input type="number" name="customer_phone" />
                            <label htmlFor="order_details">פירוט הזמנה</label>
                            <textarea name="order_details"></textarea>
                            <button>שלח</button>
                        </div>
                    }
                    {isSubmitted && 
                        <p className='submitted-form'>הבקשה נשלחה בהצלחה!</p>
                    }
                </div>
            </form>
        </div>
    );
}

export default Contact;